import React, { Component } from "react"
import {
  // CBadge,
  // CButton,
  // CButtonGroup,
  // CCard,
  // CCardBody,
  // CCardFooter,
  // CCardHeader,
  CCol,
  // CProgress,
  CRow,
  // CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Form,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
import $ from 'jquery';
import conf from '../../../conf.js';
import  { Redirect } from 'react-router-dom';
import { storage } from "../../../firebase/firebase";
// const animatedComponents = makeAnimated();

  class DetailSalle extends Component {


    constructor(props) {
      super(props)
      this.state = {
        terrainData:[],
        terrain:[],
        ok:"",
        images:[]
      }
    }

    componentDidMount() {
      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id=localStorage.getItem("idTerrain")
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
  
      fetch(conf.url+"terrain/terrains/"+id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data",data)
        this.setState({terrain:data})
         $("#nom").val(data.nom)
         $("#adresse").val(data.adresse)
         $("#ville").val(data.ville)
         $("#latitude").val(data.latitude)
         $("#longitude").val(data.longitude)
         $("#disponibilite").val(data.disponibilite)
         $("#description").val(data.description)
         $("#prixheure").val(data.prixHeure)
         $("#prixdemiheure").val(data.prixDemiHeure)

         data.prix.map((val)=>{
          $("#"+val.jour).val(val.valeur)
         })



         for(var i=0;i<data.multimedias.length;i++){
          storage.ref("images")
          .child(data.multimedias[0].file).getDownloadURL()
          .then((url) => {
     
            this.setState({images:[...this.state.images,url]})
           
          })
          
         }

        })
    }
 

    handleRetour=()=>{
      this.setState({ok: <Redirect to='/liste-salle'/>});
    }

  render(){

   
  return (
    <>
          <React.Fragment>
            <div className="page-content">
           
            {this.state.ok}

            <Container fluid={true} className="justify-content-center">

              <CRow className="justify-content-center">
              <CCol md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >DÃ©tails de la salle</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="4">
                        <label>Nom</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="nom"
                              placeholder="Nom"
                              readOnly
                          />
                      </Col>
                      <Col md="4">
                        <label>Adresse</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="adresse"
                              placeholder="Adresse"
                              readOnly
                          />
                      </Col>
                      <Col md="4">
                        <label>Ville</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="ville"
                            placeholder="ville"
                            readOnly
                        />
                      </Col>
                    </Row>
                  
                    <Row className="mt-4">
                      <Col md="12">
                        <label>Description</label>
                        <textarea  className="form-control" id="description" placeholder="Description" readOnly/>
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6">
                        <label>Latitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="latitude"
                          placeholder="Latitude"
                          readOnly
                        />
                      </Col>
                      <Col md="6">
                      <label>Longitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="longitude"
                          placeholder="Longitude"
                          readOnly
                        />
                      </Col>
                    </Row>
{/* 
                    <Row className="mt-4"> 
                      <Col md="4">
                        <label>Prix par heure</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixheure"
                          placeholder="Prix par heure"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                      <label>Prix par demi-heure</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixdemiheure"
                          placeholder="Prix par demi-heure"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                      <label>Disponibilite</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="disponibilite"
                          placeholder="Disponibilite du terrain"
                          readOnly
                        />
                      </Col>
                    </Row> */}
                    <Row className="mt-4">
                       
                        <Col md="2">  
                          <label>Prix Lundi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de lundi"
                              id="LUNDI"
                              readOnly
                          />
                         </Col>
                       
                        <Col md="2">  
                        <label>Prix Mardi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mardi"
                              id="MARDI"
                              readOnly
                          />
                         </Col>

                         <Col md="2">
                          <label>Prix Mercredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mercredi"
                              id="MERCREDI"
                              readOnly
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Jeudi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de jeudi"
                              id="JEUDI"
                              readOnly
                          />
                        </Col>
                        <Col md="2">
                          <label>Prix Vendredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de vendredi"
                              id="VENDREDI"
                              readOnly
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Samedi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de samedi"
                              id="SAMEDI"
                              readOnly
                          />
                        </Col>
                    </Row>
                    <Row className="mt-4">
                      <Col md="2">  
                        <label>Prix dimanche</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          placeholder="Prix de dimanche"
                          id="DIMANCHE"
                          readOnly
                        />
                      </Col>
                    </Row>


                    <Row className="mt-4">
                      {
                        this.state.images.map((el,idx)=>(
                          <Col md="6">
                            <Card>
                              <CardBody>
                                  <img style={{width:"460px",heigth:"420px"}} className="card-img-top" src={el} alt="Image"/>
                              </CardBody>
                            </Card>
                          </Col>
                        ))
                      }
                       </Row>

                    
                      
                    <Row className="justify-content-start mt-5">
                    <Col md="5"></Col>
                      <Col md="2">
                        <Button onClick={this.handleRetour}  color="danger" block className="b-sm">
                          Retour
                        </Button>
                      </Col>
                      <Col md="5"></Col>
                    </Row>
                  </CardBody>
                </Card>
              </CCol>
            </CRow>
          </Container>
        </div>
             
      </React.Fragment>
    
    </>
  )
}
  }
export default DetailSalle
